<template>
    <div>
        <div class="note" :style="note"></div>
        <el-container direction="vertical">
            <Header></Header>
            <el-main class="">
                <div class="timeMain">
                    <div class="sortRadio">
                        <div style="margin-left:50px;font-size:24px;display:inline-block;margin-top:40px;">排序：</div>
                        <el-radio-group v-model="reverse">
                            <el-radio :label="true">倒序</el-radio>
                            <el-radio :label="false">正序</el-radio>
                        </el-radio-group>
                    </div>
                    <el-timeline :reverse="reverse">
                        <el-timeline-item
                            v-for="(item, index) in items"
                            :key="index"
                            :timestamp="item.timestamp"
                            placement="top">
                            <el-collapse :model="item">
                                <el-collapse-item :name="item.id">
                                    <template slot="title">
                                        <span  style="font-size:18px;font-weight:600;margin-left:20px;margin-right:20px">
                                            {{ item.title }}
                                        </span>
                                    </template>
                                    <pre style="margin-left:20px;margin-right:20px;font-size:14px;font-weight:500">{{ item.content }}</pre>
                                </el-collapse-item>
                            </el-collapse>
                        </el-timeline-item>
                    </el-timeline>
                </div>
            </el-main>
        </el-container>
    </div>
</template>

<script>
export default {
    data() {
        return {
            note:{
                backgroundImage:"url(" + require("../../assets/time.jpg") + ")",
            },
            reverse:true,
            items:[
                {content:"框架初始化1", timestamp:"2019-10-20", id : "1", title : "初始化1"},
                {content:"框架初始化2", timestamp:"2019-10-20", id : "2", title : "初始化2"},
                {content:"框架初始化3", timestamp:"2019-10-20", id : "3", title : "初始化3"},
                {content:"框架初始化4", timestamp:"2019-10-20", id : "4", title : "初始化4"},
                {content:"框架初始化5", timestamp:"2019-10-20", id : "5", title : "初始化5"},
            ]
        }
    },
    methods: {
        _getTimeaxis(){
            let that = this;
            let url = DocConfig.server + '/main/timeaxis';
            let items = [];
            that.axios.get(url)
                .then((response) => {
                    if(response.data.error_code == 0){
                        console.log(response.data);
                        items = JSON.parse(response.data.data);
                        that.items.push(items);
                    }
                }).catch((error) => {
                    console.log(error)
                })
        }
    },
    mounted() {
        this._getTimeaxis();
    },
}
</script>

<style scoped>
.note{
    position: fixed;
    height: 100%;
    width: 100%;
    background: no-repeat center top;
    background-size: cover;
    z-index: -100;
    top: 0;
    left: 0;
    opacity:0.7;
}
.timeMain{
    width: 80%;
    margin: 0 auto;
}
.sortRadio{
    margin-bottom: 10px;
}
</style>


